  .close {
    position: absolute;
    right: -10px;
    top: -20px;
    i {
      font-size: 24px;
      color: grey;
    }
  }
  form {
    .form-group {
      margin-bottom: 12px;
      label {
        display: inline-block;
        min-width: 120px;
      }
    }
    &.danger {
      input {
        outline: 1px solid red;
      }
    }
  }
